body {
  margin: 0;
  font-family: 'Ubuntu', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', sans-serif;
  background-color: #000;
  color: #c3c3c3;
  font-size: 14px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-variant-numeric: lining-nums;
  font-feature-settings: "lnum";
  letter-spacing: normal;
}

p {
  color: #c3c3c3;
  line-height: 1.5;
  word-wrap: break-word;
  font-variant-numeric: lining-nums;
  font-feature-settings: "lnum";
  margin: 1em 0;
}

h1, h2, h3 {
  font-family: 'Forum', sans-serif;
  font-weight: normal;
  letter-spacing: 0.02em;
  line-height: 1.1;
  color: #f4f4f4;
  text-rendering: optimizeLegibility;
  margin: 0 0 0.5em 0;
}

h1, h2 {
  font-size: 36px;
}

h3 {
  font-size: 28px;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 1rem;
}

.input-group {
  margin-bottom: 1rem;
}

label {
  display: block;
  margin-bottom: 0.25rem;
}

input {
  width: 100%;
  padding: 0.5rem;
  border-radius: 8px;
  border: none;
  font-size: 1rem;
  background: #1f1f1f;
  color: #c3c3c3;
  font-family: inherit; /* Наследует шрифт от body */
}

.result {
  margin: 1rem 0;
  font-size: 1.2rem;
  text-align: center;
  font-weight: bold;
  color: rgb(9, 125, 47); /* Новый цвет */
  /* Общие свойства текста */
  line-height: 1.5;
  font-variant-numeric: lining-nums;
  font-feature-settings: "lnum";
  font-family: inherit; /* Гарантирует наследование Ubuntu */
}

.chart-container {
  width: 100%;
  height: auto;
  margin-top: 2rem;
}